<template>
        <el-container class="topcontainer">
            <el-header><common-header /></el-header>
            <el-container class="midcontainer">
                <el-aside><common-aside /></el-aside>
                <el-main><router-view></router-view></el-main>
            </el-container>
            <el-footer><common-footer /></el-footer>
        </el-container>
</template>

<script>
import CommonHeader from '../components/CommonHeader.vue';
import CommonAside from '../components/CommonAside.vue';
import CommonFooter from '../components/CommonFooter.vue'
export default {
    data() {
        return {}
    },
    components: {
        CommonHeader,
        CommonAside
    }
}
</script>
<style scoped>
.topcontainer {
    width: 100%;
    min-height: 100%;
}

el-header {
    position: absolute;
    display: block;
    height: 80px;
    width: 100%;
    border-bottom: 1px solid #e6e6e6;
}

.midcontainer {
    position: relative;
    display: block;
    max-width: 1280px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 120px;
    height: auto;
}

el-aside {
    position: fixed;


}

el-main {
    position: absolute;
    left: 385px;

}

</style>


